body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sky-sphere-center {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.sky-sphere-wrapper {
  width: 80%;
  height: 100%;
  -webkit-transform: translateY(25%) translateX(-10%);
          transform: translateY(25%) translateX(-10%);
  position: relative;
}

.sky-sphere {
  border-radius: 100%;
  background-color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 30px #9ecaed;
}

.sky-sphere-hue {
  box-shadow: 0 0 80px #e5ff6f;
}
.stars {
  width: 1px;
  height: 1px;
  background: transparent;
  position: absolute;
  top: 0;
  opacity: 0.7;
}
.stars::after {
  content: " ";
  position: absolute;
  top: 0;
  width: 1px;
  height: 1px;
  background: transparent;
}
.time-widget-wrapper {
  background-color: black;
  border-radius: 100%;
  position: relative;
  overflow: hidden;
}

.sky {
  background-color: lightblue;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.time-land {
  width: 100%;
  height: 100%;
  background-color: green;
  border-radius: 100%;
}

.time-land-wrapper {
  width: 100%;
  height: 50%;
  bottom: 0;
  position: absolute;
  background: darkgreen;
  border-radius: 100%;
  -webkit-transform: translateY(100%) scale(2);
          transform: translateY(100%) scale(2);
  z-index: 10;
}

.light-filter {
  width: 100%;
  height: 100%;
  -webkit-transition: opacity 2s linear;
  transition: opacity 2s linear;
  position: absolute;
  top: 0;
  left: 0;
}

.land {
  border-radius: 100%;
}

.time-land-night {
  background: rgb(3, 22, 3);
}
.time-land-night-peak {
  background: rgb(0, 48, 0);
}
.time-land-morning {
  background: darkgreen;
}
.time-land-morning-peak {
  background: darkgreen;
}
.time-land-noon {
  background: lightgreen;
}
.time-land-noon-peak {
  background: lightgreen;
}
.time-land-evening {
  background: darkgreen;
}
.time-land-evening-peak {
  background: rgb(22, 56, 22);
}

.time-sky-night {
  background: rgb(0, 0, 66);
}
.time-sky-night-peak {
  background: black;
}
.time-sky-morning {
  background: rgb(0, 0, 133);;
}
.time-sky-morning-peak {
  background: -webkit-linear-gradient(#C6E4EE 0%, #C6E4EE 40%, #FED1AE 60%, #FAA0B9 70%, #CB7DCB 80%, #757ECB 100%);
  background: linear-gradient(#C6E4EE 0%, #C6E4EE 40%, #FED1AE 60%, #FAA0B9 70%, #CB7DCB 80%, #757ECB 100%);
}
.time-sky-noon {
  background: lightblue;
}
.time-sky-noon-peak {
  background: lightblue;
}
.time-sky-evening {
  background: -webkit-linear-gradient(#C6E4EE 0%, #C6E4EE 40%, #FED1AE 60%, #FAA0B9 70%, #CB7DCB 80%, #757ECB 100%);
  background: linear-gradient(#C6E4EE 0%, #C6E4EE 40%, #FED1AE 60%, #FAA0B9 70%, #CB7DCB 80%, #757ECB 100%);
}
.time-sky-evening-peak {
  background: darkblue;
}
.showcase {
  padding: 20px;
  background-color: rgb(255, 154, 147);
  border-radius: 20px;
  border: rgb(255, 123, 114) 1px solid;
  box-shadow: 5px 0px 20px #888888;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

